import { NavLink, withRouter } from "react-router-dom";

function _AppHeader(props){

    return (
        <div className="app-header">
            <nav>
                <NavLink activeClassName="active-nav" to="/contacts"><img src={require('../assets/imgs/icons/users.png').default }alt="contacts"/></NavLink>
                <NavLink activeClassName="active-nav" to="/"><img src={require('../assets/imgs/icons/home.png').default }alt="home"/></NavLink>
                <NavLink activeClassName="active-nav" to="/graphs"><img src={require('../assets/imgs/icons/increase.png').default }alt="graphs"/></NavLink>
            </nav>
            <hr/>
        </div>
    )
}

export const appHeader = withRouter(_AppHeader);