import { Component } from 'react'
import { DailyTransChart } from '../cmps/DailyTransChart';
import { MarketPriceChart } from '../cmps/MarketPriceChart';
import { bitcoinService } from '../services/bitcoinService'

export class StatisticPage extends Component {
    state={
        marketPrice:'',
        confirmedTransactions:''
    }

    setStatsData = async () =>{
        const marketPrice = await bitcoinService.getMarketPrice();
        const confirmedTransactions = await bitcoinService.getConfirmedTransactions();
        this.setState({marketPrice, confirmedTransactions});
    }

    async componentDidMount(){
        this.setStatsData();
    }
    render() {
        const { marketPrice, confirmedTransactions} = this.state;
        return (
            <section className="Graphs-Page">
                {marketPrice && confirmedTransactions &&  <div className="graphs-containers"> 
                    <MarketPriceChart marketPrice={marketPrice}/>
                    <DailyTransChart confirmedTransactions={confirmedTransactions}/> 
                </div>}
            </section>
        )
    }
}
