import { Component, createRef } from 'react'
import { contactService } from '../services/contactService';

export class ContactEdit extends Component {

    inputRef = createRef();

    state={
        contact:null,
        errMsg:''
    }

    async componentDidMount(){
        try{
            const id = this.props.match.params.id;
            const contact = id ? await contactService.getById(id): contactService.getEmptyContact();
            this.setState({contact}, ()=>this.inputRef.current.focus());
        } catch (err) {
            this.setState({errMsg: 'Cannot find Contact'});
        }
    }

    handleChange = ({ target }) =>{
        const field  = target.name;
        const value = target.type === "number" ? +target.value : target.value;
        this.setState(prevState => ({contact: { ...prevState.contact, [field]: value}}))
    }

    onSaveContact = async (ev) =>{
        ev.preventDefault();
        const { contact } = this.state;
        await contactService.save(contact);
        this.props.history.push('/contacts');
    }


    render() {
        if (!this.state.contact) return <div>{this.state.errMsg || 'Loading...'}</div>
        const { name, phone, email} = this.state.contact;
        return (
            <form className="contact-edit">
                <label htmlFor="name">Name</label>
                <input ref={ this.inputRef} value={name} name="name" type="text" onChange= {this.handleChange}/>
                <label htmlFor="phone">Phone</label>
                <input value={phone} name="phone" type="text" onChange= {this.handleChange}/>
                <label htmlFor="email">Email</label>
                <input value={email} name="email" type="email" onChange={this.handleChange}/>
                <button onClick={ this.onSaveContact }>Save</button>
                <p>{this.state.errMsg}</p>
            </form>
        )
    }
}
