import { Component } from 'react'
import { ContactList } from '../cmps/ContactList';
import { ContactFilter } from '../cmps/ContactFilter';
import {contactService} from '../services/contactService'
import {ContactDetailsPage} from './ContactDetailsPage'
import { Link } from 'react-router-dom';
export class ContactPage extends Component {
    state={
        contacts:null,
        filterBy:{
            term:"",
        },
        //TODO: check if currContact needed
    }

    componentDidMount(){
        this.loadContacts();
    }

    async loadContacts(){
        const contacts = await contactService.getContacts(this.state.filterBy);
        this.setState({contacts});
    }

    onChangeFilter =(ev) =>{

        console.log(ev.target.value);
        this.setState(prevState => ({ filterBy: { ...prevState.filterBy, term: ev.target.value } }), this.loadContacts);       
    }

    // selectContact = (contactId) => {
    //     this.setState({selectedContactId: contactId});
    // }

    deleteContact = async (contactId) => {
        await contactService.remove(contactId);
        this.loadContacts();
    }

    

    // backToList = () => {
    //     this.props.history.push('/');
    //   };
   
    render() {
        const { contacts } = this.state
        if(!contacts) return <div className="page-loading-screen">Loading...</div>
        return (
            <div className="contact-page-container">
                <Link to="contact/edit">
                    <button>Add Contact</button>
                </Link>
                { contacts && <div className="contact-page-list">
                    <ContactFilter onChangeFilter={this.onChangeFilter}/>
                    <ContactList contacts={contacts} deleteContact={this.deleteContact}/>
                </div>}
            </div>
        )
    }
}